import {
    Component, OnInit, trigger, transition, style, animate} from '@angular/core';
import {ajaxService} from "../services/ajaxService";


@Component({
    selector: 'app-droppdown',
    templateUrl: './droppdown.component.html',
    styleUrls: ['./droppdown.component.css'],
    animations: [
        trigger(
            'enterAnimation', [
                transition(':enter', [
                    style({'max-height': '0px'}),
                    animate('500ms', style({'max-height': '200px'}))
                ]),
                transition(':leave', [
                    style({'max-height': '200px'}),
                    animate('500ms', style({'max-height': '0px'}))
                ])
            ]
        )
    ]
})
export class DroppdownComponent implements OnInit {

    private link: string = '&page=1';

    movieResults = [];
    frontData = [];
    currentTitle: any = null;
    show: boolean = false;

    showStyle: false;



    constructor(private ajax: ajaxService) {
    }

    ngOnInit() {
        this.loadMovie();
    }

    close() {
        if(this.showStyle) {
            return "open";
        } else {
            return "";
        }
    }

    getStyle() {
        if(this.showStyle) {
            return "open";
        } else {
            return "";
        }
    }




    loadMovie() {
        let resp = this.ajax.post(this.link);
        resp.subscribe(
            (resp) => {
                if (!resp.error) {
                    this.movieResults = resp.results;
                    console.log(this.movieResults);
                    for (var value in this.movieResults) {
                        this.frontData.push(this.movieResults[value])
                    }
                }
            },
            (err) => {
                console.error('Login error', err);
            }
        );
    }



}



