import {Injectable} from "@angular/core";
import {Http, Headers, RequestOptions, Response } from '@angular/http';
import {Observable} from "rxjs/Rx";
import 'rxjs/add/operator/map';

@Injectable()
export class ajaxService {

    private getDataUrl = 'https://api.themoviedb.org/3/movie/top_rated?api_key=a528038aeca96bd422081a1da3e5f20c&language=en-US';

    constructor(private http: Http) { }


    public post(link: string): Observable<any>  {
        console.debug('POST request', link);
        return this.query(link);
    }


    // TODO add in option object ", withCredentials: true"
    private query(link: string): Observable<any>  {
        let headers = new Headers({ 'Content-Type': 'application/json' }),
            options = new RequestOptions({
                headers: headers
            });

        return this.http.post(this.getDataUrl + link, options)
            .map(this.extractData)
    }
    private extractData(res: Response) {
        let body = res.json();
        console.debug('RESPONSE: ', body);
        return body || { };
    }


}



